<div
  class="baseBtn stylizedBtn"
  class:active="{concurrentPlays !== 0}"
  class:baseBtnInList
  class:pending
  class:testHoverWidth
  style="min-width: {minWidth}"
  on:click="{playSound}"
  bind:this="{self}"
  tabindex="0"
>
  {localizedName}
</div>

<script lang="ts">import { getContext, onMount } from 'svelte';
import { format, locale } from 'svelte-i18n';
;
import siteConfig from '../config';
;
import { ln } from '../utils/i18n';
const assetBasePath = siteConfig.assets_path;
// Props
export let item;
export let baseBtnInList = true;
let playerCtx;
let concurrentPlays = 0;
let pending = false;
let testHoverWidth = false;
let self;
let minWidth = '0px';
$: localizedName = ln($locale, item);
// Methods
function selectFile() {
    if (Array.isArray(item.file)) {
        return item.file[Math.floor(Math.random() * item.file.length)];
    }
    else {
        return item.file;
    }
}
function scanHoverWidth() {
    minWidth = '0px';
    testHoverWidth = true;
    requestAnimationFrame(() => {
        const width = self.offsetWidth;
        minWidth = String(width - 16 + 'px');
        testHoverWidth = false;
    });
}
export function playSound() {
    pending = true;
    const audio = playerCtx.addAudio(assetBasePath + selectFile());
    const playTimeout = setTimeout(() => {
        window === null || window === void 0 ? void 0 : window.errorFormatter($format('Voices are still loading. Please be patient...'));
    }, 1500);
    audio.addEventListener('play', () => {
        concurrentPlays++;
    });
    audio.addEventListener('pause', () => {
        concurrentPlays--;
    });
    audio
        .play()
        .then(() => {
        pending = false;
        clearTimeout(playTimeout);
    })
        .catch((e) => {
        window === null || window === void 0 ? void 0 : window.errorFormatter($format('Error in the playback:') + ' ' + String(e));
    });
}
onMount(() => {
    playerCtx = getContext('player');
    if (baseBtnInList) {
        scanHoverWidth();
        window.addEventListener('languagechange', scanHoverWidth);
    }
});
</script>

<style lang="scss">.baseBtn {
  background: #6375bc;
}
.baseBtn.testHoverWidth {
  font-weight: 900;
}

.baseBtnInList {
  min-height: 40px;
  line-height: 40px;
  margin: 12px 12px;
  padding: 4px 16px;
  border-radius: 4px;
}

.active {
  font-weight: 900;
  transition: box-shadow 0.23s ease-in-out, font-weight 0.23s ease-in-out;
}

.nonBaseBtn {
  background: linear-gradient(#6375bc, #485488);
}

.stylizedBtn {
  text-align: center;
  overflow: hidden;
  border-radius: 8px;
  cursor: pointer;
  user-select: none;
  box-shadow: 0 0.6px 1.1px rgba(0, 0, 0, 0.024), 0 1.8px 3px rgba(0, 0, 0, 0.035), 0 4.2px 7.2px rgba(0, 0, 0, 0.046), 0 14px 24px rgba(0, 0, 0, 0.07);
  transition: box-shadow 0.23s ease-in-out, font-weight 0.23s ease-in-out;
  color: white;
}
.stylizedBtn:hover {
  cursor: pointer;
  font-weight: 900;
  box-shadow: 0 2px 1.6px rgba(0, 0, 0, 0.012), 0 4.5px 3.6px rgba(0, 0, 0, 0.017), 0 7.9px 6.3px rgba(0, 0, 0, 0.021), 0 12.5px 10px rgba(0, 0, 0, 0.025), 0 19.3px 15.5px rgba(0, 0, 0, 0.029), 0 30.1px 24.1px rgba(0, 0, 0, 0.033), 0 50px 40px rgba(0, 0, 0, 0.038), 0 100px 80px rgba(0, 0, 0, 0.05);
}
.stylizedBtn:focus {
  outline: none;
  background: #8a98cd;
}

.hidden {
  display: none;
}

.pending {
  opacity: 0.8;
}</style>
